import React from 'react';
import ListTodo from './ListTodo.js';
import AddTodo from './AddTodo';
class App extends React.Component {
	state = {
		todos: [
			{
				id: 1,
				content: 'Play Games'
			},
			{
				id: 2,
				content: 'Watch Movies'
			}
		]
	};

	deleteTodo = (id) => {
		let todo = this.state.todos.filter((values) => values.id !== id);
		this.setState({
			todos: todo
		});
	};

	addTodo = (data) => {
		data.id = Math.random();
		let newTodo = [ ...this.state.todos, data ];
		this.setState({
			todos: newTodo
		});
	};
	render() {
		return (
			<div className="todo-app container">
				<h3 className="center blue-text">Todo's</h3>
				<ListTodo todos={this.state.todos} deleteTodo={this.deleteTodo} />
				<AddTodo addTodo={this.addTodo} />
			</div>
		);
	}
}
export default App;
