import React from 'react';

let ListTodo = ({ todos, deleteTodo }) => {
	let displayList = todos.length ? (
		todos.map((values) => {
			return (
				<div className="collection-item" key={values.id}>
					<span
						onClick={() => {
							deleteTodo(values.id);
						}}
					>
						{values.content}
					</span>
				</div>
			);
		})
	) : (
		<p>You dont have the todos left here...Done</p>
	);
	return <div className="todos collection">{displayList}</div>;
};

export default ListTodo;
